
import { defineComponent, ref, watchEffect } from "vue";
import { useRoute, useRouter } from "vue-router";
import { config, MenuItem } from "@/constants/menu-config";

const BrandInsightsIncludesList = [
  "BrandSummary",
  "BrandPower",
  "ProductPower",
  "FinancialPower"
];

const KYCIncludesList = ["Newsfeed", "SocialMedia"];

const AsideNavMenu = defineComponent({
  name: "AsideNavMenu",

  setup() {
    const route = useRoute();
    const router = useRouter();
    const activeIndex = ref("");

    const onRouterToHomePage = async () => {
      await router.push("/");
    };

    const onSelect = (index: string) => {
      config.submenus.forEach((submenu, submenuIndex) => {
        submenu.items.forEach(async (item: MenuItem, itemIndex) => {
          if (index === `${submenuIndex}-${itemIndex}`) {
            await router.push({ name: item.router });
          }
        });
      });
    };

    watchEffect(() => {
      let routeName = route.name as string;
      if (!routeName) return;

      if (BrandInsightsIncludesList.includes(routeName)) {
        routeName = "BrandInsights";
      }
      if (KYCIncludesList.includes(routeName)) {
        routeName = "KYC";
      }

      // 以 Router 的名稱決定一開始 Active 選項
      let isMatch = false;
      config.submenus.forEach((submenu, submenuIndex) => {
        submenu.items.forEach((item: MenuItem, itemIndex) => {
          if (item.router === routeName) {
            isMatch = true;
            activeIndex.value = `${submenuIndex}-${itemIndex}`;
          }
        });
      });
      if (!isMatch) {
        activeIndex.value = "";
      }
    });

    return {
      data: config,
      activeIndex,

      onRouterToHomePage,
      onSelect
    };
  }
});

export default AsideNavMenu;
