<template>
  <div id="chart-list-component">
    <div class="back-container">
      <font-awesome-icon
        class="icon"
        :icon="['fas', 'times']"
        @click="onBack"
      ></font-awesome-icon>
    </div>
    <h2 class="group-member-title">Group Name</h2>
    <div class="title-wrapper">
      <el-input v-model="info.title" :disabled="!isEdit">
        <template #append>
          <el-button icon="el-icon-edit" @click="onEditClick"></el-button>
        </template>
      </el-input>
      <!--<h1>{{ info.title }}</h1>-->
    </div>
    <h2 class="group-member-title">Group Member</h2>
    <div class="list-wrapper">
      <div v-for="item in info.joined_member" :key="item.id" class="list-item">
        <div class="avatar-wrapper">
          <Avatar class="avatar" :image-url="item.photo" :size="50"></Avatar>
        </div>
        <p class="name">{{ item.username }}</p>
      </div>
      <p class="the-end">-- the end --</p>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, computed } from "vue";
import { useStore } from "vuex";
import Avatar from "@/components/Avatar.vue";
import { ChatRoomListItem } from "@/typings/chatroom";
import { SetChatRoomType, PostChatRoomTitle } from "@/typings/store";

const Component = defineComponent({
  name: "ChartList",
  components: {
    Avatar
  },

  setup() {
    const store = useStore();
    const isEdit = ref(false);
    const id = computed(() => store.state.chatRoom.chatRoomId);
    const info = computed<ChatRoomListItem>(
      () => store.state.chatRoom.chatRoomInfo
    );

    const onBack = async () => {
      store.commit(SetChatRoomType, "ChatRoom");
    };

    const onEditClick = async () => {
      if (isEdit.value) {
        await store.dispatch(PostChatRoomTitle, {
          chatroom_id: id.value,
          title: info.value.title
        });
      }
      isEdit.value = !isEdit.value;
    };

    return {
      info,
      isEdit,

      onBack,
      onEditClick
    };
  }
});

export default Component;
</script>

