import request from "@/apis/http";
import { PostChatRoomMessageParams } from "@/typings/chatroom";

export function postChatRoomMessage(payload: PostChatRoomMessageParams) {
  const data = new FormData();
  data.append("chatroom", `${payload.chatroom}`);
  if (payload.message) data.append("message", payload.message);
  if (payload.message_media)
    data.append("message_media", payload.message_media);
  return request({
    url: `/message/`,
    method: "POST",
    data
  });
}
