<template>
  <div id="chart-room-component">
    <div class="top-wrapper">
      <font-awesome-icon
        class="icon"
        :icon="['fas', 'chevron-left']"
        @click="onBackClick"
      ></font-awesome-icon>
      <div
        class="avatar-wrapper"
        :style="{
          cursor: info && info.joined_member.length > 2 ? 'pointer' : ''
        }"
        @click="onMemberViewClick"
      >
        <h2 class="name">{{ info ? info.title : "" }}</h2>
        <span v-if="info && info.joined_member.length > 2">
          ({{ info.joined_member.length }})
        </span>
      </div>

      <font-awesome-icon
        class="icon-search"
        :icon="['fas', 'search']"
        @click="isSearchWrapperActive = !isSearchWrapperActive"
      ></font-awesome-icon>

      <div v-if="isSearchWrapperActive" class="search-wrapper">
        <el-input
          class="search-input"
          v-model="searchValue"
          placeholder="Search messages..."
          prefix-icon="el-icon-search"
          clearable
        >
        </el-input>
        <div class="search-console">
          <p class="number">
            {{ searchIndex + 1 }} / {{ searchMessage.results.length }}
          </p>
          <font-awesome-icon
            class="icon"
            :class="{ active: searchIndex < searchMessage.results.length - 1 }"
            :icon="['fas', 'arrow-up']"
            @click="onSearchArrowClick('up')"
          ></font-awesome-icon>
          <font-awesome-icon
            class="icon"
            :class="{ active: searchIndex > 0 }"
            :icon="['fas', 'arrow-down']"
            @click="onSearchArrowClick('down')"
          ></font-awesome-icon>
        </div>
      </div>
    </div>
    <div class="chat-wrapper" ref="chatWrapper">
      <div class="chat-container" ref="chatContainer">
        <div class="observer" ref="prevObserverWrapper"></div>
        <div
          v-for="(dayMessage, index) in formatStoreMessages(storeMessages)"
          :key="index"
        >
          <div class="time-date-container">
            <div class="time-date">
              {{ dayMessage.date }}
            </div>
          </div>
          <ChatLine
            v-for="(item, _index) in dayMessage.items"
            :key="_index"
            :is-me="item.is_me"
            :data="item"
          ></ChatLine>
          <div class="observer" ref="nextObserverWrapper"></div>
        </div>
      </div>
    </div>
    <div class="inputs-wrapper">
      <textarea
        v-model="text"
        name="text"
        id=""
        cols="30"
        rows="10"
        placeholder="Send message..."
        :style="{ resize: 'none' }"
        @keydown.enter.exact.prevent
        @keydown.enter.shift.prevent
        @keyup.enter.exact="onMessageClick"
        @keydown.enter.shift.exact="onNewline"
      ></textarea>
      <div class="console-wrapper">
        <font-awesome-icon
          class="icon"
          :icon="['fas', 'paperclip']"
          @click="onFileClick"
        ></font-awesome-icon>
        <font-awesome-icon
          class="icon"
          :icon="['fas', 'image']"
          @click="onImageClick"
        ></font-awesome-icon>
        <font-awesome-icon
          class="icon"
          :icon="['fas', 'play']"
          @click="onMessageClick"
        ></font-awesome-icon>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import {
  defineComponent,
  ref,
  computed,
  onMounted,
  watchEffect,
  nextTick
} from "vue";
import { useStore } from "vuex";
import dayjs from "dayjs";

import ChatLine from "./ChatLine.vue";

import {
  GeChatRoomHistory,
  GetChatRoomInfo,
  PostSearchMessage,
  SetChatRoomType
} from "@/typings/store";
import {
  ChatRoomListItem,
  Message,
  Messages,
  PostChatRoomMessageParams,
  SearchMessage
} from "@/typings/chatroom";
import { postChatRoomMessage } from "@/apis/chatroom";

function formatStoreMessages(data: Message[]): StoreMessage[] {
  return data.reduce((result: StoreMessage[], item: Message) => {
    if (!result.length) {
      return result.concat({
        date: dayjs(item.created_at).format("YYYY-MM-DD"),
        items: [item]
      });
    }
    if (
      result[result.length - 1].date ===
      dayjs(item.created_at).format("YYYY-MM-DD")
    ) {
      result[result.length - 1].items = result[result.length - 1].items.concat(
        item
      );
      return result;
    } else {
      return result.concat({
        date: dayjs(item.created_at).format("YYYY-MM-DD"),
        items: [item]
      });
    }
  }, []);
}

interface StoreMessage {
  date: string;
  items: Message[];
}

const Component = defineComponent({
  name: "ChartRoom",

  components: { ChatLine },

  setup() {
    const store = useStore();

    const storeMessages = ref<Message[]>([]);
    const flagHistories = ref(-1);
    const flagMessages = ref(-1);

    const text = ref("");
    const prevCursor = ref("");
    const nextCursor = ref("");

    const searchValue = ref("");
    const chatWrapper = ref<Element | null>(null);
    const chatContainer = ref<Element | null>(null);

    const prevObserverWrapper = ref<Element | null>(null);
    const nextObserverWrapper = ref<Element | null>(null);

    const isSearchWrapperActive = ref(false);
    const isLoad = ref(false);
    const isFinished = ref(false);
    const searchIndex = ref(0);
    const timeId = ref<ReturnType<typeof setTimeout> | undefined>(undefined);

    const chatRoomId = computed(() => store.state.chatRoom.chatRoomId);
    const info = computed<ChatRoomListItem>(
      () => store.state.chatRoom.chatRoomInfo
    );
    const histories = computed<Messages>(() => store.state.chatRoom.histories);
    const messages = computed<Messages>(() => store.state.chatRoom.messages);
    const searchMessage = computed<SearchMessage>(
      () => store.state.chatRoom.searchMessage
    );

    // 向上加載
    watchEffect(async () => {
      if (histories.value && !histories.value.next_curosr) {
        isFinished.value = true;
      }

      if (
        histories.value &&
        histories.value.results.length &&
        histories.value.results[0].id !== flagHistories.value
      ) {
        const results: Message[] = histories.value.results;
        flagHistories.value = results[0].id;

        nextCursor.value = histories.value.next_curosr;
        storeMessages.value = histories.value.results.concat(
          storeMessages.value
        );

        await nextTick(() => {
          if (chatWrapper.value) {
            chatWrapper.value.scrollTo(0, 300);
          }
        });
      }
    });

    // 向下加載
    watchEffect(async () => {
      if (
        messages.value &&
        messages.value.results.length &&
        messages.value.results[messages.value.results.length - 1].id !==
          flagMessages.value
      ) {
        const results: Message[] = messages.value.results;
        flagMessages.value = results[results.length - 1].id;

        const findIndex = storeMessages.value.findIndex(
          (it: Message) => it.id === results[0].id
        );

        if (findIndex >= 0) {
          storeMessages.value = [
            ...storeMessages.value.slice(0, findIndex),
            ...results,
            ...storeMessages.value.slice(findIndex + results.length)
          ];
        } else {
          storeMessages.value = storeMessages.value.concat(
            messages.value.results
          );
        }

        await nextTick(() => {
          if (chatWrapper.value && chatContainer.value) {
            chatWrapper.value.scrollTo(0, chatContainer.value.clientHeight);
          }
        });
      }
    });

    const initIntersectionObserver = () => {
      const options = {};

      const callback = (entries: IntersectionObserverEntry[]) => {
        entries.forEach(async entry => {
          if (entry.isIntersecting && !isLoad.value && !isFinished.value) {
            isLoad.value = true;
            await store.dispatch(GeChatRoomHistory, {
              id: chatRoomId.value,
              cursor: nextCursor.value
            });
            isLoad.value = false;
          }
        });
      };
      const observer = new IntersectionObserver(callback, options);
      if (prevObserverWrapper.value) {
        observer.observe(prevObserverWrapper.value);
      }
    };

    // *** Search Zoom
    watchEffect(() => {
      if (!isSearchWrapperActive.value) return;

      const message = searchValue.value;
      timeId.value && clearTimeout(timeId.value);
      timeId.value = setTimeout(async () => {
        await store.dispatch(PostSearchMessage, {
          chatroom_id: chatRoomId.value,
          message
        });
      }, 500);
    });

    watchEffect(async () => {
      if (!isSearchWrapperActive.value) return;

      const results = searchMessage.value.results;
      searchIndex.value = results.length > 0 ? 0 : -1;
    });

    watchEffect(async () => {
      if (!isSearchWrapperActive.value) return;

      const results = searchMessage.value.results;
      if (results.length) {
        // @ts-ignore
        const target = results[searchIndex.value];
        isLoad.value = true;
        await store.dispatch(GeChatRoomHistory, {
          id: chatRoomId.value,
          // @ts-ignore
          cursor: target.direct_cursor
        });
        isLoad.value = false;
      }
    });

    // ***

    const init = async () => {
      await store.dispatch(GetChatRoomInfo, chatRoomId.value);
      initIntersectionObserver();
    };

    onMounted(init);

    const onBackClick = () => {
      store.commit(SetChatRoomType, "ChatList");
    };

    const onMemberViewClick = () => {
      if (!info.value || info.value.joined_member.length <= 2) return;
      store.commit(SetChatRoomType, "ChatMembers");
    };

    const onFileClick = () => {
      const element = document.createElement("input");
      element.type = "file";
      element.accept = "pdf/*";
      element.multiple = true;
      element.addEventListener("change", async () => {
        if (element.files) {
          const params: PostChatRoomMessageParams = {
            chatroom: chatRoomId.value,
            message_media: element.files[0]
          };
          await postChatRoomMessage(params);
        }
      });
      element.click();
    };

    const onImageClick = () => {
      const element = document.createElement("input");
      element.type = "file";
      element.accept = "image/*";
      element.addEventListener("change", async () => {
        if (element.files) {
          const params: PostChatRoomMessageParams = {
            chatroom: chatRoomId.value,
            message_media: element.files[0]
          };
          await postChatRoomMessage(params);
        }
      });
      element.click();
    };

    const onNewline = () => {
      text.value = `${text.value}\n`;
    };

    const onMessageClick = async () => {
      if (!text.value) return;
      const params: PostChatRoomMessageParams = {
        chatroom: chatRoomId.value,
        message: `${text.value}`
      };
      text.value = "";
      await postChatRoomMessage(params);
    };

    const onSearchArrowClick = (type: string) => {
      if (
        type === "up" &&
        searchIndex.value < searchMessage.value.results.length - 1
      ) {
        searchIndex.value += 1;
      }

      if (type === "down" && searchIndex.value > 0) {
        searchIndex.value -= 1;
      }
    };

    return {
      chatWrapper,
      chatContainer,
      prevObserverWrapper,
      nextObserverWrapper,

      info,
      storeMessages,
      text,
      searchValue,
      searchIndex,
      isSearchWrapperActive,
      searchMessage,

      onBackClick,
      onSearchArrowClick,
      onFileClick,
      onImageClick,
      onNewline,
      onMessageClick,
      onMemberViewClick,

      formatStoreMessages,
      dayjs
    };
  }
});

export default Component;
</script>

