import request from "@/apis/http";
import { AxiosPromise } from "axios";
import { CreateMembersParams } from "@/typings/auth";

export type LoginParams = {
  username: string;
  password: string;
};

export type LoginResponse = {
  access: string;
  refresh: string;
};

export type TokenRefreshParams = {
  refresh: string;
};

export function postCreateMembers(payload: CreateMembersParams) {
  return request({
    url: `/members/`,
    method: "POST",
    data: payload
  });
}

export function postLogin(payload: LoginParams): AxiosPromise<LoginResponse> {
  return request({
    url: "/token/",
    method: "POST",
    data: payload
  });
}

export function postTokenRefresh(
  payload: TokenRefreshParams
): AxiosPromise<LoginResponse> {
  return request({
    url: "/token/refresh/",
    method: "POST",
    data: payload
  });
}

export function getPermission() {
  return request({
    url: "/permission/buttons_display/",
    method: "GET"
  });
}

export function getInvitation(key: string) {
  return request({
    url: `/invitation/${key}/`,
    method: "GET"
  });
}

export function postCreateInvitation(payload: any) {
  return request({
    url: `/invitations/`,
    method: "POST",
    data: payload
  });
}
