
import { defineComponent, ref, computed, onMounted } from "vue";
import { useStore } from "vuex";
import Avatars from "./Avatars.vue";
import { ChatRoomListItem } from "@/typings/chatroom";
import {
  ClearChatRoom,
  GetChatRoomList,
  PostChatRoomRead,
  SetChatRoomId,
  SetChatRoomType
} from "@/typings/store";

const Component = defineComponent({
  name: "ChartList",
  components: {
    Avatars
  },

  setup() {
    // Last message 會顯示最後一句話或者會後上傳的檔案
    function getLastMessage(item: ChatRoomListItem): string {
      if (!item.last_message || item.last_message.index === null) return "";
      if (item.last_message.message_type === "text") {
        return item.last_message.message;
      }
      return `updated ${item.last_message.message_type} ${
        item.last_message.message_media!.name
      }`;
    }

    const store = useStore();

    const searchValue = ref("");
    const list = computed<ChatRoomListItem[]>(() => store.state.chatRoom.list);

    const filteredList = computed(() => {
      if (!searchValue.value) return list.value;
      return list.value.filter((it: ChatRoomListItem) =>
        it.title.includes(searchValue.value)
      );
    });

    const init = async () => {
      await store.dispatch(GetChatRoomList);
    };

    onMounted(init);

    const onItemClick = async (id: number) => {
      store.commit(ClearChatRoom);
      if (!list.value.find((it: ChatRoomListItem) => it.id === id)!.is_read) {
        await store.dispatch(PostChatRoomRead, id);
      }
      store.commit(SetChatRoomId, id);
      store.commit(SetChatRoomType, "ChatRoom");
    };

    const onCreateClick = () => {
      store.commit(SetChatRoomType, "ChatCreate");
    };
    return {
      searchValue,
      filteredList,

      onItemClick,
      onCreateClick,

      getLastMessage
    };
  }
});

export default Component;
