
import { defineComponent, ref, computed } from "vue";
import { useStore } from "vuex";
import Avatar from "@/components/Avatar.vue";
import { ChatRoomListItem } from "@/typings/chatroom";
import { SetChatRoomType, PostChatRoomTitle } from "@/typings/store";

const Component = defineComponent({
  name: "ChartList",
  components: {
    Avatar
  },

  setup() {
    const store = useStore();
    const isEdit = ref(false);
    const id = computed(() => store.state.chatRoom.chatRoomId);
    const info = computed<ChatRoomListItem>(
      () => store.state.chatRoom.chatRoomInfo
    );

    const onBack = async () => {
      store.commit(SetChatRoomType, "ChatRoom");
    };

    const onEditClick = async () => {
      if (isEdit.value) {
        await store.dispatch(PostChatRoomTitle, {
          chatroom_id: id.value,
          title: info.value.title
        });
      }
      isEdit.value = !isEdit.value;
    };

    return {
      info,
      isEdit,

      onBack,
      onEditClick
    };
  }
});

export default Component;
