<template>
  <!--For Full-page-->
  <template v-if="isFullPage">
    <div class="layout">
      <router-view></router-view>
    </div>
  </template>

  <Layout v-else>
    <template #main>
      <router-view></router-view>
    </template>
  </Layout>
</template>

<script lang="ts">
import { defineComponent, computed, onMounted } from "vue";
import { useRoute } from "vue-router";
import Layout from "@/components/Layout.vue";
import { useStore } from "vuex";
import { GetPermission, EmitResizer } from "@/typings/store";
import { findPermission } from "@/utils";

const Component = defineComponent({
  name: "App",
  components: {
    Layout
  },
  setup() {
    const store = useStore();
    const route = useRoute();
    const fullPageWhiteList = ["InviteConfirm", "Landing"];

    const init = async () => {
      if (!findPermission()) {
        await store.dispatch(GetPermission);
        location.reload();
      }
      window.onresize = () => {
        store.commit(EmitResizer);
      };
    };

    onMounted(init);

    const isFullPage = computed((): boolean => {
      if (typeof route.name !== "string") return false;
      return fullPageWhiteList.includes(route.name);
    });

    return {
      isFullPage
    };
  }
});

export default Component;
</script>

