
import { defineComponent, ref, computed, onMounted } from "vue";
import { useStore } from "vuex";
import {
  CreateChatRoomRoom,
  GetChatRoomMembers,
  SetChatRoomType
} from "@/typings/store";
import { ChatRoomMember } from "@/typings/chatroom";
import Avatars from "./Avatars.vue";

type Group = {
  id: number;
  name: string;
};

const Component = defineComponent({
  name: "ChatCreate",
  components: { Avatars },
  setup() {
    const store = useStore();

    const groupValue = ref<Group[]>([]);
    const inputValue = ref("");

    const members = computed<ChatRoomMember[]>(
      () => store.state.chatRoom.members
    );

    const list = computed(() => {
      if (!inputValue.value && !groupValue.value.length) return members.value;

      return members.value.filter(
        (it: ChatRoomMember) =>
          it.username.indexOf(inputValue.value) !== -1 &&
          !groupValue.value.some((_it: Group) => _it.name === it.username)
      );
    });

    const getMembers = async (ids: number[] | null) => {
      await store.dispatch(GetChatRoomMembers, ids);
    };

    const init = async () => {
      await getMembers(null);
    };

    onMounted(init);

    const onItemClick = async (data: ChatRoomMember) => {
      const group: Group = { id: data.id, name: data.username };
      groupValue.value = groupValue.value.concat(group);
      inputValue.value = "";
      const ids = groupValue.value.map((it: Group) => it.id);
      await getMembers(ids);
    };

    const onTagClose = async (id: number) => {
      groupValue.value = groupValue.value.filter(it => it.id !== id);
      const ids = groupValue.value.map((it: Group) => it.id);
      await getMembers(ids);
    };

    const onBackClick = () => {
      store.commit(SetChatRoomType, "ChatList");
    };

    const onCreateClick = async () => {
      const ids = groupValue.value.map((it: Group) => it.id);
      await store.dispatch(CreateChatRoomRoom, { ids });
    };

    return {
      list,
      groupValue,
      inputValue,

      onItemClick,
      onTagClose,
      onBackClick,
      onCreateClick
    };
  }
});

export default Component;
