<template>
  <div class="avatar-component" :style="componentStyle">
    <font-awesome-icon v-if="!imageUrl" class="icon" :icon="['fas', 'user']"></font-awesome-icon>
  </div>
</template>

<script>
import { computed, toRefs } from "vue";
import { formatImageUrl } from "../utils";
export default {
  name: "Avatar",
  props: {
    imageUrl: {
      type: String,
      default: ""
    },
    size: {
      type: Number,
      default: 100
    }
  },
  setup(props) {
    const { size, imageUrl } = toRefs(props);
    const componentStyle = computed(() => ({
      width: `${size.value}px`,
      height: `${size.value}px`,
      fontSize: `${size.value * 0.4}px`,
      backgroundImage: `url('${formatImageUrl(imageUrl.value)}')`
    }));

    return {
      componentStyle
    };
  }
};
</script>

<style lang="stylus" scoped>
.avatar-component
  flex()
  border 1px solid $color-black
  border-radius 50%
  background white
  background-repeat no-repeat
  background-position center
  background-size contain
</style>
