<template>
  <div id="layout-component">
    <el-aside width="260px" :style="{ height: '100%', overflow: 'hidden' }">
      <AsideNavMenu></AsideNavMenu>
    </el-aside>
    <el-container>
      <el-header height="50px" :style="{ padding: 0 }">
        <Header></Header>
      </el-header>
      <el-main class="el-main">
        <div class="el-main-wrapper">
          <slot name="main"></slot>
        </div>
      </el-main>
    </el-container>
    <div
      class="chat-room-wrapper"
      :class="{ 'chat-room-active': active }"
      @click.stop
    >
      <ChatRoom></ChatRoom>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed } from "vue";
import { useStore } from "vuex";
import AsideNavMenu from "@/components/AsideNavMenu.vue";
import Header from "@/components/Header.vue";
import ChatRoom from "@/components/ChatRoom/index.vue";
import { HEADER_COLLAPSE_TYPE } from "@/constants/header";

const Component = defineComponent({
  name: "Layout",
  components: {
    Header,
    AsideNavMenu,
    ChatRoom
  },
  setup() {
    const store = useStore();

    const active = computed(() => {
      return store.state.headerCollapseType === HEADER_COLLAPSE_TYPE.CHAT_ROOM;
    });

    return {
      active
    };
  }
});

export default Component;
</script>

