<template>
  <div id="chart-list-component">
    <div class="top-wrapper">
      <h1>Messenger</h1>
      <font-awesome-icon
        class="icon"
        :icon="['fas', 'comment-medical']"
        @click="onCreateClick"
      ></font-awesome-icon>
    </div>

    <el-input
      class="search-input"
      v-model="searchValue"
      placeholder="Search chart room..."
    ></el-input>

    <div class="list-wrapper">
      <div
        v-for="item in filteredList"
        :key="item.id"
        class="list-item"
        :class="{ 'is-unread': !item.is_read }"
        @click="onItemClick(item.id)"
      >
        <Avatars class="avatar" :data="item.joined_member"></Avatars>
        <div class="content-wrapper">
          <div class="name-container">
            <h4 class="name">{{ item.title }}</h4>
            <span v-if="item.joined_member.length > 2">
              ({{ item.joined_member.length }})
            </span>
          </div>
          <p class="text">
            {{ getLastMessage(item) }}
          </p>
        </div>
      </div>
      <p class="the-end">-- the end --</p>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, computed, onMounted } from "vue";
import { useStore } from "vuex";
import Avatars from "./Avatars.vue";
import { ChatRoomListItem } from "@/typings/chatroom";
import {
  ClearChatRoom,
  GetChatRoomList,
  PostChatRoomRead,
  SetChatRoomId,
  SetChatRoomType
} from "@/typings/store";

const Component = defineComponent({
  name: "ChartList",
  components: {
    Avatars
  },

  setup() {
    // Last message 會顯示最後一句話或者會後上傳的檔案
    function getLastMessage(item: ChatRoomListItem): string {
      if (!item.last_message || item.last_message.index === null) return "";
      if (item.last_message.message_type === "text") {
        return item.last_message.message;
      }
      return `updated ${item.last_message.message_type} ${
        item.last_message.message_media!.name
      }`;
    }

    const store = useStore();

    const searchValue = ref("");
    const list = computed<ChatRoomListItem[]>(() => store.state.chatRoom.list);

    const filteredList = computed(() => {
      if (!searchValue.value) return list.value;
      return list.value.filter((it: ChatRoomListItem) =>
        it.title.includes(searchValue.value)
      );
    });

    const init = async () => {
      await store.dispatch(GetChatRoomList);
    };

    onMounted(init);

    const onItemClick = async (id: number) => {
      store.commit(ClearChatRoom);
      if (!list.value.find((it: ChatRoomListItem) => it.id === id)!.is_read) {
        await store.dispatch(PostChatRoomRead, id);
      }
      store.commit(SetChatRoomId, id);
      store.commit(SetChatRoomType, "ChatRoom");
    };

    const onCreateClick = () => {
      store.commit(SetChatRoomType, "ChatCreate");
    };
    return {
      searchValue,
      filteredList,

      onItemClick,
      onCreateClick,

      getLastMessage
    };
  }
});

export default Component;
</script>

