import * as _ from "ramda";
import { FilterOption, OptionItem, SliderOption } from "@/typings/asideFilter";

export const watchFilterOptions = (options: FilterOption[]) => {
  return _.flatten(
    options.map(option => {
      return option.items.filter(item => item.checked).map(item => item.label);
    })
  );
};

export const closeTag = (options: FilterOption[], tag: string): FilterOption[] => {
  return options.map((option: FilterOption): FilterOption => {
    return {
      ...option,
      items: option.items.map(item => {
        if (item.label === tag) {
          return {
            ...item,
            checked: false
          };
        }
        return item;
      })
    };
  });
};

export const closeAllTags = (options: FilterOption[]): FilterOption[] => {
  return options.map((option: FilterOption): FilterOption => {
    return {
      ...option,
      items: option.items.map(item => ({
        ...item,
        checked: false
      }))
    };
  });
};

export const createFilterOptions = (
  data: any,
  params: { title: string; key: string, default?: string }[]
): FilterOption[] => {
  return params.map((item): FilterOption => ({
    title: item.title,
    items: data[item.key]
      ? data[item.key].map((it: OptionItem) => ({
          ...it,
          checked: it.value === item.default
        }))
      : []
  }));
};

export const createSliderOptions = (
  data: any,
  params: { title: string; key: string }[]
): SliderOption[] => {
  return params.map((item): SliderOption => ({
    title: item.title,
    range: { min: data[item.key].min, max: data[item.key].max },
    value: [data[item.key].min, data[item.key].max],
    marks: {
      [data[item.key].min]: `${data[item.key].min}`,
      [data[item.key].max]: `${data[item.key].max}`
    }
  }));
};

export const createCompositionOptions = (
  data: any,
  params: { title: string; key: string, default?: string }[]
): FilterOption[]=> {
  return []
};
