<template>
  <div id="notification-collapse-component">
    <el-collapse-transition>
      <el-card class="card" v-show="isShow">
        <template v-if="list.results.length">
          <div
            v-for="(item, index) in list.results"
            :key="index"
            class="item"
            :class="{ unread: !item.is_read }"
            @click="onItemClick(item)"
          >
            <img
              :src="formatImageUrl(item.fabric_main_image + '@50-50')"
              alt=""
            />
            <div>
              <p class="text">
                {{ item.inner_text }}
              </p>
              <p class="time">
                {{ dayjs(item.created_at).format("YYYY-MM-DD hh:mm:ss") }}
              </p>
            </div>
          </div>
          <div class="load-more-button-container">
            <el-button v-if="list.next_curosr" type="success" @click.stop="onLoadMoreClick">
              Load More
            </el-button>
          </div>
        </template>
        <div v-else>
          <p>No Notification</p>
        </div>
      </el-card>
    </el-collapse-transition>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
import dayjs from "dayjs";
import { formatImageUrl } from "@/utils";
import { PostNotificationRead, PostNotifications } from "@/typings/store";
import { MyNotification } from "@/typings/chatroom";
import { HEADER_COLLAPSE_TYPE } from "@/constants/header";

const Component = defineComponent({
  name: "NotificationCollapse",
  setup() {
    const store = useStore();
    const router = useRouter();
    const isShow = computed(
      () => store.state.headerCollapseType === HEADER_COLLAPSE_TYPE.NOTIFICATION
    );
    const list = computed(
      () =>
        store.state.chatRoom.notifications || { results: [], next_curosr: "" }
    );

    const onItemClick = async (item: MyNotification) => {
      if (
        !list.value.results.find((it: MyNotification) => it.id === item.id)!
          .is_read
      ) {
        await store.dispatch(PostNotificationRead, item.id);
        await store.dispatch(PostNotifications);
      }
      await router.push(item.url);
    };

    const onLoadMoreClick = async () => {
      await store.dispatch(PostNotifications, list.value.next_curosr);
    };

    return {
      isShow,
      list,

      close,
      onItemClick,
      onLoadMoreClick,

      dayjs,
      formatImageUrl
    };
  }
});

export default Component;
</script>

