
import { defineComponent, computed } from "vue";
import { useRouter } from "vue-router";
import { ElNotification } from "element-plus";
import { removeRefreshToken, removeToken } from "@/utils/tokenStorage";
import { GetPermission } from "@/typings/store";
import { useStore } from "vuex";
import { HEADER_COLLAPSE_TYPE } from "@/constants/header";

const Component = defineComponent({
  name: "UserCollapse",
  setup() {
    const store = useStore();
    const router = useRouter();

    const isShow = computed(
      () => store.state.headerCollapseType === HEADER_COLLAPSE_TYPE.USER
    );

    const onProfileClick = async () => {
      await router.push({ name: "MyProfile" });
    };

    const onChangePasswordClick = async () => {
      await router.push({ name: "ChangePassword" });
    };

    const onInviteBrandClick = async () => {
      await router.push({ name: "InviteBrand" });
    };

    const onColleaguesClick = async () => {
      await router.push({ name: "Colleagues" });
    };

    const onCompaniesClick = async () => {
      await router.push({ name: "Companies" });
    };

    const onLogOutClick = async () => {
      removeToken();
      removeRefreshToken();
      await store.dispatch(GetPermission);
      ElNotification({ type: "success", message: "Log Out Successfully!" });
      await router.push({ name: "Login" });
      location.reload();
    };

    return {
      isShow,

      close,
      onProfileClick,
      onChangePasswordClick,
      onInviteBrandClick,
      onColleaguesClick,
      onCompaniesClick,
      onLogOutClick
    };
  }
});

export default Component;
