<template>
  <div id="chart-room-index-component">
    <ChatList v-if="type === 'ChatList'"></ChatList>
    <ChatCreate v-if="type === 'ChatCreate'"></ChatCreate>
    <ChatRoom v-if="type === 'ChatRoom'"></ChatRoom>
    <ChatMembers v-if="type === 'ChatMembers'"></ChatMembers>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed, onMounted } from "vue";
import { useStore } from "vuex";

import ChatList from "./ChatList.vue";
import ChatCreate from "./ChatCreate.vue";
import ChatRoom from "./ChatRoom.vue";
import ChatMembers from './ChatMembers.vue'
import { ConnectChatRoom } from "@/typings/store";

const Component = defineComponent({
  name: "ChartRoomIndex",
  components: {
    ChatList,
    ChatCreate,
    ChatRoom,
    ChatMembers
  },
  setup() {
    const store = useStore();

    const type = computed(() => store.state.chatRoom.type);

    const init = async () => {
      await store.dispatch(ConnectChatRoom);
    };

    onMounted(init);

    return {
      type
    };
  }
});

export default Component;
</script>

<style lang="stylus" scoped>
#chart-room-index-component
  background white
  width 350px
  height calc(100vh - 50px)
  box-shadow 0 12px 28px 0 rgba(0, 0, 0, 0.1);
</style>
