<template>
  <div id="chat-create-component">
    <div class="top-wrapper">
      <font-awesome-icon
        class="icon"
        :icon="['fas', 'chevron-left']"
        @click="onBackClick"
      ></font-awesome-icon>
      <!--<el-input v-model="nameValue" placeholder="Chat room name..."></el-input>-->
    </div>

    <div class="group-input-wrapper">
      <h2>To:</h2>
      <div class="group-input">
        <el-tag
          v-for="tag in groupValue"
          :key="tag.id"
          closable
          @close="onTagClose(tag.id)"
        >
          {{ tag.name }}
        </el-tag>
        <label>
          <input
            class="search-input"
            v-model="inputValue"
            type="text"
            placeholder="Search... "
          />
        </label>
      </div>
    </div>

    <div class="list-wrapper">
      <div
        v-for="item in list"
        :key="item.id"
        class="list-item"
        @click="onItemClick(item)"
      >
        <Avatars class="avatar" :data="[item]"></Avatars>
        <div class="content-wrapper">
          <p class="name">{{ item.username }}</p>
        </div>
      </div>
      <p class="the-end">-- the end --</p>
    </div>

    <div class="bottom-wrapper">
      <el-button type="success" @click="onCreateClick">Create</el-button>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, computed, onMounted } from "vue";
import { useStore } from "vuex";
import {
  CreateChatRoomRoom,
  GetChatRoomMembers,
  SetChatRoomType
} from "@/typings/store";
import { ChatRoomMember } from "@/typings/chatroom";
import Avatars from "./Avatars.vue";

type Group = {
  id: number;
  name: string;
};

const Component = defineComponent({
  name: "ChatCreate",
  components: { Avatars },
  setup() {
    const store = useStore();

    const groupValue = ref<Group[]>([]);
    const inputValue = ref("");

    const members = computed<ChatRoomMember[]>(
      () => store.state.chatRoom.members
    );

    const list = computed(() => {
      if (!inputValue.value && !groupValue.value.length) return members.value;

      return members.value.filter(
        (it: ChatRoomMember) =>
          it.username.indexOf(inputValue.value) !== -1 &&
          !groupValue.value.some((_it: Group) => _it.name === it.username)
      );
    });

    const getMembers = async (ids: number[] | null) => {
      await store.dispatch(GetChatRoomMembers, ids);
    };

    const init = async () => {
      await getMembers(null);
    };

    onMounted(init);

    const onItemClick = async (data: ChatRoomMember) => {
      const group: Group = { id: data.id, name: data.username };
      groupValue.value = groupValue.value.concat(group);
      inputValue.value = "";
      const ids = groupValue.value.map((it: Group) => it.id);
      await getMembers(ids);
    };

    const onTagClose = async (id: number) => {
      groupValue.value = groupValue.value.filter(it => it.id !== id);
      const ids = groupValue.value.map((it: Group) => it.id);
      await getMembers(ids);
    };

    const onBackClick = () => {
      store.commit(SetChatRoomType, "ChatList");
    };

    const onCreateClick = async () => {
      const ids = groupValue.value.map((it: Group) => it.id);
      await store.dispatch(CreateChatRoomRoom, { ids });
    };

    return {
      list,
      groupValue,
      inputValue,

      onItemClick,
      onTagClose,
      onBackClick,
      onCreateClick
    };
  }
});

export default Component;
</script>

