              data[0].photo ? formatImageUrl(data[0].photo) : defaultAvatarImage
            }')`
          }"
        ></div>
      </template>
      <template v-else>
        <div
          class="avatars"
          :style="{
            backgroundImage: `url('${
              data[0].photo ? formatImageUrl(data[0].photo) : defaultAvatarImage
            }')`,
            top: '30%',
            left: '30%'
          }"
        ></div>
        <div
          class="avatars"
          :style="{
            backgroundImage: `url('${
              data[1].photo ? formatImageUrl(data[1].photo) : defaultAvatarImage
