<!--suppress ALL -->
<template>
  <div id="header-component">
    <div class="icons-group">
      <el-badge
        v-if="isLogIn"
        v-permission="'chatroom_module'"
        :value="chatRoomUnread"
        :hidden="!chatRoomUnread"
        class="badge"
        @click.stop
      >
        <img :src="chatIcon" alt="chat icon" @click="onChatClick" />
      </el-badge>

      <el-badge :hidden="true" class="badge" type="primary" @click.stop>
        <img
          v-if="isLogIn"
          :src="userIcon"
          alt="user icon"
          @click="onUserClick"
        />
        <p v-else class="login" role="button" @click="onLogInClick">Log In</p>
      </el-badge>

      <el-badge
        v-if="isLogIn"
        v-permission="'notification_module'"
        :value="notificationUnread"
        :hidden="!notificationUnread"
        class="badge"
        @click.stop
      >
        <img
          :src="notificationIcon"
          alt="notification icon"
          @click="onNotificationClick"
        />
      </el-badge>
    </div>

    <NotificationCollapse
      v-permission="'notification_module'"
      v-if="isLogIn"
    ></NotificationCollapse>
    <UserCollapse v-if="isLogIn"></UserCollapse>
  </div>
</template>

<script lang="ts">
import {
  defineComponent,
  ref,
  computed,
  Ref,
  onMounted,
  onBeforeUnmount
} from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";

import NotificationCollapse from "@/components/NotificationCollapse.vue";
import UserCollapse from "@/components/UserCollapse.vue";
import { findToken } from "@/utils";

import { SetHeaderCollapseType } from "@/typings/store";
import { HEADER_COLLAPSE_TYPE } from "@/constants/header";
import notificationIcon from "@/assets/images/icons/png/notification.png";
import userIcon from "@/assets/images/icons/png/user.png";
import chatIcon from "@/assets/images/icons/png/chat.png";

const Component = defineComponent({
  name: "Header",
  components: {
    NotificationCollapse,
    UserCollapse
  },
  setup() {
    const store = useStore();
    const router = useRouter();
    const isLogIn = ref(findToken());

    const listener: Ref<EventListener | null> = ref(null);
    const chatRoomUnread = computed(() => store.state.chatRoom.chatRoomUnread);
    const notificationUnread = computed(
      () => store.state.chatRoom.notificationUnread
    );

    onMounted(() => {
      listener.value = (document.addEventListener("click", () => {
        store.commit(SetHeaderCollapseType, null);
      }) as unknown) as EventListener;
    });

    onBeforeUnmount(() => {
      if (!listener.value) return new Error("Listener not found.");
      document.removeEventListener("click", listener.value);
    });

    const onNotificationClick = () => {
      store.commit(SetHeaderCollapseType, HEADER_COLLAPSE_TYPE.NOTIFICATION);
    };
    const onUserClick = () => {
      store.commit(SetHeaderCollapseType, HEADER_COLLAPSE_TYPE.USER);
    };
    const onLogInClick = async ()=> {
      store.commit(SetHeaderCollapseType, null);
      await router.push({ name: "Login" });
    };
    const onChatClick = () => {
      store.commit(SetHeaderCollapseType, HEADER_COLLAPSE_TYPE.CHAT_ROOM);
    };

    return {
      isLogIn,
      chatRoomUnread,
      notificationUnread,
      notificationIcon,
      userIcon,
      chatIcon,

      onNotificationClick,
      onUserClick,
      onLogInClick,
      onChatClick
    };
  }
});

export default Component;
</script>

