import { Router } from "vue-router";
import { findToken } from "@/utils";

const whiteList = [
  "/login",
  "/invite-confirm",
  "/landing",
  "/fabrics",
  "/collections"
]; // no redirect whitelist

const dynamicWhiteList = ["/fabric/", "/collection/"];

export function installPermission(router: Router) {
  router.beforeEach((to, from, next) => {
    if (findToken()) {
      /* has token*/
      next();
    } else {
      /* has no token*/
      if (
        whiteList.includes(to.path) ||
        dynamicWhiteList.some((path: string) => to.path.indexOf(path) !== -1)
      ) {
        // 在免登入白名單，直接進入
        next();
      } else {
        next("/landing"); // 否則全部重新定向到登入頁
      }
    }
  });
}
