<template>
  <div id="chat-line-component" :class="{ me: isMe, other: !isMe }">
    <p class="chat-time" v-if="isMe">
      {{ dayjs(data.created_at).format("hh:mm:ss") }}
    </p>

    <div class="box-wrapper">
      <Avatars v-if="!isMe" :data="[data.user]" has-name></Avatars>
      <div class="chart-box" :class="isMe ? 'me' : 'other'">
        <img
          v-if="data.message_type === 'image'"
          :src="formatImageUrl(data.message_media.file)"
          alt=""
          @click="onOpen(data.message_media.file)"
        />
        <div
          v-else-if="data.message_type === 'pdf'"
          class="file-wrapper"
          @click="onOpen(data.message_media.file)"
        >
          <font-awesome-icon
            class="icon"
            :icon="['fas', 'file']"
          ></font-awesome-icon>
          <p>{{ data.message_media.name }}</p>
        </div>

        <!-- message_type === 'text' -->
        <template v-else>
          <p v-for="(msg, index) in data.message.split(/\n/)" :key="index">
            {{ msg || "&nbsp;" }}
          </p>
        </template>
      </div>
    </div>

    <p class="chat-time" v-if="!isMe">
      {{ dayjs(data.created_at).format("hh:mm:ss") }}
    </p>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType } from "vue";
import * as dayjs from "dayjs";
import Avatars from "./Avatars.vue";
import { Message } from "@/typings/chatroom";
import { formatImageUrl } from "@/utils";

const Component = defineComponent({
  name: "ChatLine",
  components: {
    Avatars
  },
  props: {
    isMe: {
      type: Boolean,
      default: false
    },
    data: {
      type: Object as PropType<Message>,
      default: () => ({})
    }
  },
  setup() {
    const onOpen = (url: string) => {
      window.open(formatImageUrl(url));
    };
    return {
      onOpen,

      dayjs,
      formatImageUrl
    };
  }
});

export default Component;
</script>

