  font-size $font-size-large
  font-weight 600
  padding 8px 0
  .chat-time
    padding 0 5px
    display inline-block
    font-size $font-size-small

  .box-wrapper
    flex(space-between)
    max-width 80%
    display inline-block
  .chart-box
    display inline-block
    padding 15px
    border-radius 15px
    word-break break-all
    img
      cursor pointer
      width 100%
      height auto
    .file-wrapper
      flex()
      flex-direction column
      cursor pointer
      p
        text-align center
      .icon
        width 50px
        height auto
        margin-bottom 8px
  .me
    margin-right 15px
    background $color-active
    border-top-right-radius 0
  .other
    margin-top 8px
    margin-left 30px
    background $color-gray
    border-top-left-radius 0

.me
  flex(flex-end, flex-end)

.other
  flex(flex-start, flex-end)
</style>
